import React, { useState } from 'react';
import { Dropdown } from 'rsuite';


const Navbar = () => {
    const [Menu, setMenu] = useState(false);

    return (
        <>
            <nav className='w-screen relative flex justify-between items-center shadow-md px-10 py-5'>
                <div>
                    <div className='font-bold text-3xl'>Mashable<span className='font-light text-sm text-white'> India</span></div>
                </div>
                <div className='lg:flex hidden justify-between gap-5 font-bold'>
                    <div>Podcast</div>
                    <div>Culture</div>
                    <div>Tech</div>
                    <div>Science</div>
                </div>
                
                <Dropdown className='relative lg:hidden' title={<div onClick={() => setMenu(!Menu)}>
                    <i className="fa-solid fa-bars text-3xl cursor-pointer"></i>
                </div>} noCaret>
                    <div className='absolute top-4 -right-4 bg-zinc-900/60 p-5 rounded text-md font-medium' style={{color:'#ccc'}}>
                    <Dropdown.Item className='cursor my-1 mr-2'>podcast</Dropdown.Item>
                    <Dropdown.Item className='cursor my-1 mr-2'>culture</Dropdown.Item>
                    <Dropdown.Item className='cursor my-1 mr-2'>tech</Dropdown.Item>
                    <Dropdown.Item className='cursor my-1 mr-2'>science</Dropdown.Item>
                    </div>
                </Dropdown>
            </nav>

        </>
    )
}

export default Navbar
