import React, { useEffect, useState } from 'react'
import axios from 'axios'
import { everything } from './apieverything';
import { Link } from 'react-router-dom';

const Newsm = () => {
    const [Data, setData] = useState({});
    useEffect(() => {
        fetchData();
    }, [Data]);
    const fetchData = async () => {
        try {
            const res = await axios.get('https://newsapi.org/v2/everything?q=bitcoin&apiKey=d828d3193bf74f43af1fbc38101a7041');
            const main = res.data.articles
            setData(main)
            //console.log(Data)
        } catch (error) {
            //console.log(error)
            setData(everything)
            
        }
    }
    function checkIfImageExists(url) {
        const img = new Image();
        img.src = url;
        
        if (img.complete) {
          return true
        } else {
          img.onload = () => {
            return false
          };
          
          img.onerror = () => {
            return false
          };
        }
      }


    return (
        <>
            {Data && Data.length > 0 ? (
                Data.map((data, index) => (
                    <>
                    {checkIfImageExists(data.urlToImage) && (
                    <Link to={`/NewsPage/${index}`}
                        className="shadow-lg bg-zinc-100/10 mt-5 flex justify-between rounded-lg min-h-[120px] overflow-hidden"
                        key={data.id || index}
                    >
                        <img
                            className="rounded-l-lg object-cover object-center h-full w-[120px]"
                            alt={data.title}
                            src={data.urlToImage}
                        />
                        <div className="flex flex-col p-2">
                            <h2 className="font-bold text-lg truncate..."> {data.content ? `${data.content.slice(0, 20)}...` : ''}</h2>
                            <h4 className="font-light text-xs">
                                {data.content ? `${data.content.slice(0, 500)}...` : ''}
                            </h4>
                        </div>
                    </Link>)}
    </>
                ))
            ) : (
                <div>Loading...</div>
            )}

        </>
    )
}

export default Newsm
