import React, { useEffect, useState } from 'react'
import axios from 'axios'
import { everything } from './apieverything';
import { useParams } from 'react-router-dom';

const NewsPage = () => {
    const {id} =useParams()
    const [Data, setData] = useState({});
    useEffect(() => {
        fetchData();
    }, [Data]);
    const fetchData = async () => {
        try {
            const res = await axios.get('https://newsapi.org/v2/everything?q=bitcoin&apiKey=d828d3193bf74f43af1fbc38101a7041');
            const main = res.data.articles
            setData(main)
            //console.log(Data)
        } catch (error) {
            setData(everything)
        }
    }
    
  return (
    <>
    {Data && Data.length > 0 ? (
        Data.map((data, index) => (
            <>
            {index==id && (
            <div className='flex justify-center items-center'>
            <div className='flex flex-col shadow-lg bg-zinc-100/10 mt-5 justify-between rounded-lg min-h-[450px] lg:max-w-[60vw] max-w-[90vw]'  key={data.id || index}>
                <img className='rounded-l-lg object-cover object-center'  alt={data.title} src={data.urlToImage}  />
                <div className='flex flex-col p-4'>
                    <h2 className="font-bold text-lg">{data.title}</h2>
                    <h4 className="font-light text-xs">{data.content ? `${data.content.slice(0, 100)}...` : ''}</h4>
                </div>
            </div>
            </div>
            )}
            </>
        ))
    ) : (
        <div>Loading...</div>
    )}
    </>
  )
}

export default NewsPage
